.popup {
    position: fixed;
    display: flex;
    visibility: hidden;
    transition: 0.5s;
    opacity: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgba(0, 0, 0, .5);
}